module.exports = {
    template: require('./create.template.html'),
    data() {
        return {
            source: null,
            task: '',
            nextPriority: 1
        };
    },
    ready() {
        this.source = this.$db.child('todo-lists/' + this.$getUser().uid);
    },
    methods: {
        add() {
            this.setPriority();

            this.source.push({
                title: this.task,
                completed: false,
                '.priority': this.nextPriority
            });
            this.task = '';
        },
        setPriority() {
            this.source.once('value', function (snapshot) {
                this.nextPriority = snapshot.numChildren() + 1;
            }.bind(this));
        }
    }
};
